 .loginScreen{
    position: relative;
    height: 100%;
    background: url("https://miro.medium.com/max/1400/1*5lyavS59mazOFnb55Z6znQ.png")center no-repeat;
    background-size: cover;
 }

 .loginScreen__background{
    display: flex;
    justify-content: space-between;
 }

 .loginScreen_logo{
    position: fixed;
    width: 100px;
    left:20px;
    top: 20px;
    object-fit: contain;
    padding-left: 20px;
}

.loginScreen__button{
    position: fixed;
    right: 20px;
    top: 20px;
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
    z-index: 100;
}
.loginScreen__body{
    position: absolute;
    top:30%;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    z-index: 1;
    color: #fff;
    padding: 20px;
}

.loginScreen__body > h1{
    font-size: 3.125rem;
    margin-bottom: 20px;
}

.loginScreen__body > h2{
    font-size: 2rem;
    font-weight: 400;
    margin-bottom: 30px;
}
.loginScreen__body > h3{
    font-size: 1.3rem;
    font-weight: 400;

}

.loginScreen__gradient{
    width: 100%;
    height: 100vh;
    background: rgba(0,0,0,0.3);
    background-image: linear-gradient(
        to top,
        rgba(0,0,0,0.8) 0,
        rgba(0,0,0,0) 60%,
        rgba(0,0,0,.8) 100%
    );
}
.loginScreen__input{
    margin: 20px;
}
.loginScreen__input > form > input{
    padding: 10px;
    outline-width: 0;
    height: 30px;
    width: 30%;
    border: none;
    max-width: 600px;
}
.loginScreen_getStarted{
    padding: 16px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
}