.row{
    color: white;
    margin-left: 20px;
}

.row__posters{
display: flex;
overflow-y: hidden;
overflow-x: scroll;
padding: 20px;
}

.row__posters::-webkit-scrollbar{
    display: none;
}

.row__poster{
    max-height: 100px;
    object-fit: contain;
    margin-right: 10px;
    width: 100%;
    transition: transform 400ms;
}

.row__poster:hover{
    transform: scale(1.1);
    opacity: 1;
}
.row__posterLarge{
    max-height: 250px;
    object-fit: contain;
    margin-right: 10px;
    width: 100%;
    transition: transform 450ms;
}

