.banner{
    position: relative;
 
    object-fit: contain;
    color: white;
}

.banner__contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}
.banner__title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: .3rem;
}
.banner__description{
    width: 45rem;
    line-height: 1.3rem;
    padding-top: 1rem;
    font-size: .8rem;
    max-width: 360px;
    height: 80px;
}
.banner--fadeBottom{
    height: 7.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37,37,37,.61),
        #111
    );
}

.banner_button{
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: .2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: .5rem;
    padding-bottom: .5rem;
    background-color: rgba(51,51,51,0.5);
    transition: all .5s;
}
.banner_button:hover{
    color: black;
    background-color: #e6e6e6;
    
}